<template>
  <div class="singer">
    <div class="back" @click.stop="$router.go(-1)">&nbsp;&lt; 返回</div>
      <div class="singer-top">
        <img :src="artistdetail.artist.cover" alt="" />
        <div class="singer-info">
          <div class="name">{{artistdetail.artist.name}}</div>
          <div class="rank">歌手榜华语地区 NO.{{artistdetail.artist.rank.rank}}</div>
          <div class="fans">粉丝 {{artistdetail.artist.fansSize}}746.2万</div>
        </div>
     
    </div>

    <div class="singer-ms">
      <div class="ms-title">歌手简介</div>
      <div :class="[{smdetail:!detailstaute},{bigdetail:detailstaute}]">{{artistDec.briefDesc}}</div>
      <div class="btn" @click.stop="changeDetail">{{detailstaute==false?'完整歌手介绍':'收起'}}</div>
    </div>
    <div class="hot-song">
      <div class="hot-title">热门单曲</div>
      <music-list :musiclist="songslist" @play="$emit('play',$event)" :shownum="shownum"></music-list>
    </div>
    <div class="loadmore" ref='loadmore'>"没有更多啦(●'◡'●)"</div>
  </div>
</template>

<script>
import MusicList from "@/components/MusicList.vue";
export default {
  // props: {
  //   singerId: {
  //     type: Number,
  //     default: 0,
  //   },
  // },
  data() {
    return {
      singerId:'',
      artistdetail: [],
      artistDec: [],
      detailstaute:false,
      songslist:[],
       shownum:true,
    };
  },
  
  methods: {
    async getArtistDetail() {
      await this.axios.get(`/artist/detail?id=${this.singerId}`).then((re) => {
        this.artistdetail = re;
        console.log("artistdetail=>", this.artistdetail);
      });
    },
    async getArtistDec() {
      await this.axios.get(`/artist/desc?id=${this.singerId}`).then((re) => {
        this.artistDec = re;
         console.log('artistDec=>',this.artistDec);
      });
    },
     async getSongsList() {
      await this.axios.get(`/artists?id=${this.singerId}`).then((re) => {
        this.songslist = re.hotSongs.map((item)=>{
               item.song={
                 alias:item.alia,
                //  privilege:re.privilege[index],
                 artists:item.ar,
                 album:item.al
               }
               return item
             });
        console.log("songslist=>", this.songslist);
      });
    },
    changeDetail(){
      if(this.detailstaute==false){
        this.detailstaute=true;
        return;
      }else{
        this.detailstaute=false;
      }
    }
  },
  created(){
    this.singerId=this.$route.params.id;    
    this.getArtistDetail();
     this.getArtistDec();
     this.getSongsList();
    //  console.log( "this.singerId",this.singerId);
  },

  mounted() {
    // console.log('=======>',this.artistDetail);
  },
  components: {
    MusicList,
  },
};
</script>

<style lang="scss" scoped>
div.singer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  overflow: auto;
  z-index: 9997;
  .back {
    z-index: 222;
    color: black;
    font-size: 18px;
    height: 20px;
    padding: 2px 0;
  }
  div.singer-top {
    width: 100%;
    position: relative;
    img {
      //   display: block;
      width: 100%;
    }
    .singer-info {
      height: 80px;
      position: absolute;
      bottom: 25px;
      left: 20px;
      color: white;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      div.name {
        font-size: 20px;
      }

      div.rank {
        font-size: 13px;
      }
      div.fans {
        font-size: 13px;
      }
    }
  }
  div.singer-ms {
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    div.ms-title {
      font-size: 18px;
      font-weight: bold;
    }
    .smdetail {
      font-size: 14px;
      color: #666;
      margin-top: 20px;
      height: 56px;
      letter-spacing: 1px;
      line-height: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .bigdetail{
      font-size: 14px;
      color: #666;
      margin-top: 20px;
      line-height: 20px;
    }
    .btn{
      padding-top: 20px;
      text-align: center;
      font-size: 14px;
    }
  }

  div.hot-song {
    margin-top: 20px;
    margin-left: 10px;

    div.hot-title {
      font-size: 18px;
      font-weight: bold;
    }
  }
  .loadmore{
   text-align: center;
   font-size: 14px;
   line-height: 40px;
 }
}
</style>
